import {
  faLaptopCode,
  faCode,
  faDatabase,
  faCogs,
  faServer,
  faGlobe,
  faSitemap,
  faCubes,
  faChartBar,
  faPalette,
  faTerminal,
  faBoxOpen,
} from '@fortawesome/free-solid-svg-icons';

export const technologies = {
  'Front-end Technologies': ['CSS3', 'HTML5', 'JavaScript', 'React', 'Bootstrap'],
  'Back-end Technologies': ['Node.js', 'Express.js', 'Java', 'SpringBoot', 'Hibernate'],
  'Database Technologies': ['MongoDB', 'PostgreSQL', 'SQL', 'DBeaver'],
  'Version Control & Collaboration': ['GitHub', 'Git'],
  'Server & Deployment': ['Linux', 'Docker'],
  'Testing & Frameworks': ['Postman', 'JUnit5', 'UML'],
  'HTTP & Web Services': ['HTTP'],
  IDEs: ['Visual Studio Code (VS Code)', 'IntelliJ IDEA'],
  'Operating Systems': ['Windows 10/11', 'Ubuntu 20.04/22.04'],
  Browser: ['Chrome', 'Brave', 'Firefox', 'Browser Developer Tools'],
  'Image Editing Software': ['Adobe Photoshop', 'GIMP'],
  'Development Tools': ['Terminal/Command Line', 'Node Package Manager'],
};

export const getIconForTechnology = (tech) => {
  const techMap = {
    Linux: faLaptopCode,
    CSS3: faCogs,
    HTML5: faCode,
    JavaScript: faCode,
    'Node.js': faServer,
    GitHub: faGlobe,
    Git: faGlobe,
    'Express.js': faServer,
    HTTP: faGlobe,
    MongoDB: faDatabase,
    React: faSitemap,
    Java: faCode,
    JUnit5: faChartBar,
    PostgreSQL: faDatabase,
    UML: faSitemap,
    Bootstrap: faCubes,
    Docker: faCubes,
    Hibernate: faSitemap,
    SpringBoot: faSitemap,
    SQL: faDatabase,
    'Visual Studio Code (VS Code)': faLaptopCode,
    'IntelliJ IDEA': faLaptopCode,
    DBeaver: faDatabase,
    'Windows 10/11': faCogs,
    'Ubuntu 20.04/22.04': faCogs,
    Chrome: faCubes,
    Brave: faCubes,
    Firefox: faCubes,
    'Browser Developer Tools': faCubes,
    Postman: faChartBar,
    'Adobe Photoshop': faPalette,
    GIMP: faPalette,
    'Terminal/Command Line': faTerminal,
    'Node Package Manager': faBoxOpen,
  };

  return techMap[tech] || faCode; // default icon
};
