import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Container } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { getIconForTechnology } from '../../mock/aboutMe/techStackData';
import '../../style/pages/_techStack.scss';

// TODO:
// * make this a component with hard/softskills?

const generateUniqueKey = () => {
  return Math.random().toString(36).substring(7);
};

const TechStack = ({ technologies }) => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 600);
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <Container fluid className="techstack-page-container">
      <Row className={`techstack-row ${isMobile ? 'mobile-layout' : ''}`}>
        {Object.keys(technologies).map((category) => (
          <Col lg={4} sm={12} key={category} className="techstack-column">
            <div className="techstack-card">
              <h2 className="techstack-category-heading">{category}</h2>
              {technologies[category].map((tech) => (
                <div className="techstack-item" key={generateUniqueKey()}>
                  <FontAwesomeIcon icon={getIconForTechnology(tech)} className="techstack-icon" />
                  <span className="techstack-text">{tech}</span>
                </div>
              ))}
            </div>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

TechStack.propTypes = {
  technologies: PropTypes.objectOf(PropTypes.arrayOf(PropTypes.string)).isRequired,
};

export default TechStack;
